import { Spacing } from '@zepdev/design-system-component-library-react';
import clsx from 'clsx';
import { createContext, useContext } from 'react';
import { GlobalVariants } from '../../interfaces/global-variants';
import { Button } from '../zps-button/ZpsButton';
import { ZpsButtonVariant } from '../zps-button/zps-button.interface';
import {
  HeroArrowIconProps,
  HeroBodyProps,
  HeroButtonProps,
  HeroHeadlineProps,
  HeroImageProps,
  HeroProps,
} from './hero.interface';
import { cva } from 'class-variance-authority';
import { twMerge } from 'tailwind-merge';

const HeroContext = createContext({
  variant:GlobalVariants.Zps,
});
export const useHeroContext = () => useContext(HeroContext);

const Hero = ({ variant =  GlobalVariants.Zps,children }: HeroProps) => {
  return (
    <HeroContext.Provider value={{ variant }}>
      <div
        className={clsx(
          'zep-flex-col',
          'zep-rounded-lg',
          'zep-shadow-lg',
          'md:zep-flex',
          'zep-relative',
          'zep-max-w-[1920px]',
          'zep-mx-auto',
        )}
        data-testid="zep-newhero"
      >
 {children}       
      </div>
       </HeroContext.Provider>
  );
};

const HeroImage = ({ imageSrc, imageAlt }: HeroImageProps) => {
  return (
    <div data-testid="" className={clsx('sm:zep-h-auto', 'md:zep-h-[380px]', 'lg:zep-h-[540px]', 'zep-flex')}>
      <img
        alt={imageAlt}
        src={imageSrc}
        data-testid="zep-newhero-image"
        className={clsx(
          'zep-w-full',
          'zep-object-cover',
          'zep-h-[202px]',
          'sm:zep-h-[380px]',
          'md:zep-h-[380px]',
          'lg:zep-h-[540px]',
          'md:zep-max-w-full',
        )}
      ></img>
    </div>
  );
};

const HeroBody = ({ children }: HeroBodyProps) => {

  const { variant } = useHeroContext();

  const heroThemes = {
    [GlobalVariants.Zps]: ['zep-bg-gradient-to-r zep-from-[rgb(32,27,52)] zep-to-[rgb(32,23,94)]'],
    [GlobalVariants.Cat]: ['zep-bg-typography-dark-100'],
  };

  const heroCva = cva(
    [
      ` 
        zep-px-[24px]
        zep-py-[32px]
        sm:zep-px-[40px]
        sm:zep-py-[48px]
        md:zep-px-[65px]
        md:zep-py-[64px]
        lg:zep-py-[80px]
        lg:zep-px-[122px]
        zep-items-center
        zep-w-full
        zep-relative
      `
    ],
    {
      variants: {
        variant: heroThemes,
      },
      defaultVariants: {
        variant: GlobalVariants.Zps,
      },
    },
  );

  return (
    <div
      data-testid="zep-newhero-body"
      className={twMerge(heroCva({ variant }))}
    >
      <div className="sm:zep-w-9/12">{children}</div>
    </div>
  );
};

const HeroHeader = ({ children }: HeroHeadlineProps) => {
  return (
    <h4
      data-testid="new-hero-headline"
      className={clsx(
        'zep-typography-headlineLG',
        'zep-typography-headline2XL-fluid-cqi',
        'supports-cqi:zep-typography-headline2XL-fluid-cqi',
        'zep-text-typography-light-100',
        'supports-cqi:after:zep-text-[0.23em]',
        'zep-mb-2',
        'zep-col-span-1',
      )}
    >
      {children}
    </h4>
  );
};

const HeroButton = ({ buttonPrimary, buttonSecondary }: HeroButtonProps) => {
  return (
    <div className="zep-block" data-testid="zep-new-herobutton">
      <Spacing className={clsx('max-md:zep-flex', 'sm:zep-flex-row', 'zep-gap-1')} direction="column">
        <Button label={buttonPrimary} variant={ZpsButtonVariant.Primary}></Button>
        <Button label={buttonSecondary} variant={ZpsButtonVariant.Secondary} />
      </Spacing>
    </div>
  );
};

const HeroIconArrow = ({ imageSrc, alt }: HeroArrowIconProps) => {
  return (
    <div className="zep-w-full zep-absolute zep-left-[0px] zep-bottom-[0px]">
      <img
        src={imageSrc}
        alt={alt}
        width="18.75%"
        data-testid="zep-her-arrowIcon"
        className={clsx(
          'zep-absolute',
          'zep-hidden',
          'zep-right-1',
          'md:zep-block',
          'md:zep-mr-[3%]',
          'md:zep-top-[50%]',
          'md:zep-translate-y-[-50%]',
        )}
      ></img>
    </div>
  );
};

Hero.Image = HeroImage;
Hero.Body = HeroBody;
Hero.Header = HeroHeader;
Hero.Button = HeroButton;
Hero.IconArrow = HeroIconArrow;

export { Hero };